$color-background:  #11C4DD;
$color-white-text: #fff;
$color-important-text: #5181B8; 
$gradient-background: linear-gradient(90deg, #0F5EA0 0%, #1B93E1 100%);

*, *:after, *:before {
  box-sizing: border-box;
  -webkit-appearance: none;
  appearance: none;
  outline: 0;
  /* for Internet Explorer */
  background: none;
}

html, body  {
  overflow-x: hidden;
  max-width: 100vw;
  font-family: 'Ubuntu', sans-serif;
}
body {
  font-size: (15rem/16);
  @media (min-width: 1000px)  {
    font-size: (17rem/16);
  }
}
body.overflowY, html.overflowY {
  overflow-y: hidden;
}
strong {
  font-weight: bold;
}

.normalize-ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.h1, .h2 {

}
.h1 {
  font-size: (26rem/16);
  @media (min-width: 1000px) {
    font-size: (33rem/16);
  }
}
.h2 {
  font-size: (18rem/16);
  @media (min-width: 1000px) {
    font-size: (22rem/16);
  }
}
.text-14 {
  font-size: (12rem/16);
  @media (min-width: 1000px) {
    font-size: (14rem/16);
  }
}
p {
  margin: (25em/17) 0;
}
.no-margin {
  margin: 0;
}

.container, .conainer-block {
  width: 100%;
  padding: 0 20px;
}
.container {
  @media (min-width: 1041px) {
    padding: 0 calc(50% - 500px);
  }
}
.conainer-block {
  max-width: 1040px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: inherit;
  &.visible {
    color: $color-important-text;
    &:hover {
      text-decoration: underline;
    }
  }
  &.with-icon {
    display: inline-block;
    & > * {
      vertical-align: middle;
    }
    svg, img {
      margin-right: 5px;
    }
  }
  &.with-icon-df {
    display: flex;
    align-items: center;
    svg, img {
      margin-right: 5px;
    }
  }
}

.section {
  max-width: 100vw;
  &__wrapper {
    padding-top: 60px;
    padding-bottom: 60px;
    width: 100%;
    &--two-column {
      display: flex;
      align-items: center;
      margin: 0 -15px;
   
      &__1, &__2 {
        padding: 0 15px;
      }
    }
    &__title {
      font-weight: normal;
      text-align: center;
      margin-top: 0;
      margin-bottom: (60em/33);
    }
  }
  &__picture img{ 
    position: absolute;
    top: -100%;
    right: -100%;
    bottom: -100%;
    left: -100%;
    margin: auto;
    min-width: 100%;
    min-height: 100%;
    z-index: -1;
  }
}
.section--backimg {
  position: relative;
  overflow: hidden;
}
.section--full {
  display: flex;
  align-items: center;
  &::before {
    content: '';
    display: block;
    width: 0;
    height: 100vh;
    max-height: 700px;
  }
}
.section--gradient {
  background: $gradient-background;
  color: $color-white-text;
}

.btn--reset {
  @include button-reset;
}
.btn {
  cursor: pointer;
  display: inline-flex;
  text-decoration: none;
  min-width: (210em/19);
  line-height: 1;
  font-size: (16rem/16);
  text-align: center;
  align-items: center;
  justify-content: center;
  padding: (13em/19) (25em/19);
  transition: background-color 0.2s, color 0.2s;
  @media (min-width: 1000px) {
    font-size: (19rem/16);
  }
  &--full-white {
    background: #fff;
    color: $color-important-text;
    border: 2px solid #ffffff;
    &:hover {
      color: #ffffff;
      background: none;
    }
  }
  &--gradient {
    background: $gradient-background;
    border: 0;
    color: #fff;
    font-size: (19rem/16);
    border-radius: 5px;
    font-weight: 500;
    @media (min-width: 1000px) {
      font-size: (22rem/16);
    }
  }
  &--line {
    background: none;
    color: #fff;
    border: 2px solid #fff;
    &:hover {
      background: #ffffff;
      color: $color-important-text;
    }
  }
  &--rounded {
    border-radius: 100px;
  }
}

.input {
  border: 1px solid rgba(#000000, 0.5);
  border-radius: 5px;
  color: #000000;
  max-width: 100%;
  background: #ffffff;
  @include input-placeholder {
    opacity: 1;
    color: rgba(#000000, 0.2);
  }
  &--big {
    padding: 0 (20em/22);
    font-size: (19rem/16);
    line-height: 1.4;
    min-height: (63em/22);
    @media (min-width: 1000px) {
      font-size: (22rem/16);
    }
  }
  &--base {
    font-size: (17rem/16);
    padding: 0 (20em/17);
    line-height: 1.4;
    min-height: (43em/17);
  }
  &--with-icon {
    display: inline-flex;
    align-items: center;
    padding: 0px 15px;
    border: 1px solid #000;
    svg {
      margin-right: 18px;
      flex: 0 0 auto;
    }
    input {
      flex: 1 1 auto;
      border: 0;
      padding: 0;
      font-size: (19rem/17);
      line-height: 1.4;
      min-height: (63em/22);
      @media (min-width: 1000px)  {
        font-size: (22rem/16);
      }
      @include input-placeholder {
        opacity: 1;
        color: rgba(#000000, 0.2);
      }
    }
  }
  &--center {
    text-align: center;
  }
}
.textarea {
  border: 1px solid rgba(#000000, 0.5);
  border-radius: 5px;
  color: #000000;
  max-width: 100%;
  font-size: (17rem/16);
  padding: (12em/17) (20em/17);
  line-height: 1.2;
  height: 140px;
  resize: none;
  background: #ffffff;
  @include input-placeholder {
    opacity: 1;
    color: rgba(#000000, 0.2);
  }
}