.modal-background {
  background: #000000;
  opacity: 0.5;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  display: none;
  &.active {
    display: block;
  }
}
.modal-window {
  background: #F7F7F7;
  box-shadow: 0px 22px 44px -12px rgba(0, 0, 0, 0.35);
  border-radius: 10px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1001;
  max-width: calc(100vw - 30px);
  max-height: 95vh;
  display: none;
  overflow-y: auto;
  &.active {
    display: block;
  }
  &__wrapper {
    padding: 45px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    width: 100%;
    @media (min-width: 500px) {
      padding: 45px calc(29.333vw - 126.5px);
    }
    @media (min-width: 650px) {
      padding: 45px 64px;
    }
    & > * {
      max-width: 100%;
    }
  }
  form {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
  }
  &__exit {
    position: absolute;
    top: 10px;
    right: 10px;
    height: 26px;
    width: 27px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: rotate(45deg);
    &:before, &:after {
      content: '';
      display: block;
      width: 26px;
      height: 3px;
      border-radius: 3px;
      background: #7b7b7b;
      position: absolute;
      top: 50%;
      left: 0;
    }
    &:before {
      transform: rotate(90deg) translate(0, -0%);
    }
    &:after {
      transform: rotate(0deg) translate(0, -0%);
    }
  }
}
.modal-window--succes {
  width: 517px;
  height: 186px;
  @media (min-width: 400px) {
    height: 256px;
  }
  &.active {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.modal-window--signin {
  width: 709px;
  .h2 {
    margin-bottom: 15px;
    font-weight: 300;
  }
  input, textarea {
    margin-top: 8px;
    margin-bottom: 8px;
  }
  textarea {
    margin-bottom: 16px;
  }
  .inputs {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 -10px;
    input {
      width: calc(50% - 20px);
      min-width: 250px;
      flex: 1 1 auto;
      margin-right: 10px;
      margin-left: 10px;
    }
  }
}
.modal-window--signup {
  width: 619px;
  .h2 {
    font-weight: 500;
    margin-bottom: 55px;
  }
  form > input {
    margin-bottom: 10px;
    text-align: center;
  }
  form > input:last-of-type {
    margin-bottom: 0;
  }
  form > a {
    align-self: center;
    margin-top: 5px;
  }
  form {
    max-width: 279px;
    margin: 0 auto;
  }
  .checkbox {
    border: 1px solid #000000;
    border-radius: 2px;
    width: 10px;
    height: 10px;
    position: relative;
    margin-right: 3px;
    input {
      top: -1px;
      width: 10px;
      height: 10px;
      left: -1px;
      right:-1px;
      bottom: -1px;
      position: absolute;
      &:checked + svg {
        opacity: 1;
      }
    }
    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-30%, -50%);
      opacity: 0;
    }
  }
  &__remember {
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    margin-top: 9px;
    cursor: pointer;
  }
}