.homepage-section--1 {
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  .btn--full-white {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    text-shadow: none;
    &:hover {
      box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.15), 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
  }
  .btn--line {
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.15), 0px 4px 4px rgba(0, 0, 0, 0.25);
    text-shadow: none;
    &:hover {
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
  }
  .h1 {
    margin-bottom: (25em/33);
    width: 100%;
    span {
      font-size: (35%*100/33);
    }
  }
  .h2 {
    width: 100%;
    font-weight: 300;
    margin-bottom: (25em/22);
  }
  .section__wrapper--two-column {
    align-self: stretch;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    @media (min-width: 900px) {
      margin: 0 -7px;
      justify-content: flex-start;
      flex-wrap: nowrap;
    }
  }
  .section__wrapper--two-column__1, .section__wrapper--two-column__2 {
    padding: 0;
    flex: 1 1 100%;
    @media (min-width: 900px) {
      padding: 0 7px;
      flex: 1 1 0%;
    }
  }
  .section__wrapper--two-column__1 {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-bottom: 30px;
    text-align: center;
    @media (min-width: 900px) {
      text-align: left;
      height: 100%;
      max-height: 249px;
      margin-bottom: 0;
    }
    @media (min-width: 1000px) {
      max-height: 289px;
    }
  }
  .section__wrapper--two-column__2 {
    text-align: center;
    img {
      max-width: 100%;
      @media (min-width: 900px) {
        margin-top: -50px;
      }
    }
  }
  &__button {
    margin: auto -11px 0;
    display: inline-flex;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    max-width: 464px;
    align-self: center;
    @media (min-width: 900px) {
      align-self: flex-start;
    }
    .btn {
      flex: 1 1 0%;
      margin: 11px;
    }
  }
}

.homepage-section--2 {
  .section__wrapper--two-column {
    flex-direction: column;
    align-items: center;
    @media (min-width: 780px) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  p:last-child {
    margin-bottom: 0;
  }
  p:first-child {
    margin-top: 0;
  }
  .section__wrapper--two-column__2 {
    margin-top: 40px;
    max-width: 515px;
    width: 100%;
    @media (min-width: 780px) {
      max-width: none;
      width: 421px;
    }
  }
  .section__wrapper--two-column__1 {
    flex: 0 1 auto;
    width: 100%;
    max-width: 515px;
    @media (min-width: 780px) {
      width: auto;
      max-width: none;
    }
    img {
      max-width: 100%;
    }
  }
}

.homepage-section--3 {
  background: rgba(#000, 0.5);
  color: $color-white-text;
  &__block {
    position: relative;
    padding: 54px 0 0 45px;
    z-index: 1;
    margin: 25px auto;
    width: 100%;
    max-width: 455px;
    @media (min-width: 800px) {
      margin: 0;
      max-width: none;
    }
    .h1 {
      font-weight: normal;
    }
    p {
      font-weight: 300;
    }
    svg {
      top: 0;
      left: 0;
      position: absolute;
      transform: translate(0%, 30%);
      z-index: -1;
      opacity: 0.9;
    }
    &--1 {
      margin-top: 0;
      @media (min-width: 800px) {
        width: 42%;
      }
      @media (min-width: 1300px) {
        margin-left: -9.4%;
      }
    }
    &--2 {
      @media (min-width: 800px) {
        width: 48.8%;
        transform: translate(0%, 100%);
        margin-top: -8%;
      }
    }
    &--3 {
      @media (min-width: 800px) {
        width: 43.9%;
        transform: translate(128%, -100%);
        margin-top: 4%;
      }
    }
    &--4 {
      margin-bottom: 0;
      @media (min-width: 800px)  {
        width: 50%;
        transform: translate(100%, 0);
        margin-top: -3%;
      }
      @media (min-width: 1300px) {
        margin-top: -7%;
        transform: translate(118.8%, 0);
      }
    }
  }
}

.homepage-section--4 {
  &__grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 25px;
    @media (min-width: 700px) {
      flex-wrap: wrap-reverse;
    }
    p {
      max-width: 210px;
      margin-right: auto;
      margin-left: auto;
      width: 100%;
    }
    &__item {
      text-align: center;
      width: 100%;
      padding: 25px 15px 0;
      @media (min-width: 450px) {
        width: 50%;
        max-width: none;
      }
      @media (min-width: 700px) {
        width: 33.333%;
      }
      @media (min-width: 1041px) {
        padding: 25px 40px 0;
      }
      &--1, &--2 {
        @media (min-width: 700px) {
          order: 1;
        }
      }
    }
  }
}

.homepage-section--5 {
  &__row {
    display: flex;
    align-items: flex-start;
    margin-bottom: 35px;
    flex-direction: column;
    @media (min-width: 450px) {
      flex-direction: row;
    }
    @media (min-width: 1000px) {
      max-width: 810px;
    }
    svg {
      margin-right: 20px;
      margin-bottom: 15px;
      flex-shrink: 0;
      @media (min-width: 450px) {
        margin-top: 10px;
        margin-bottom: 0;
      }
    }
    .h1 {
      font-weight: normal;
      margin-bottom: 4px;
    }
    &--last {
      margin-bottom: 0;
    }
  }
  &__svg {
    display: none;
    @media (min-width: 1000px) {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(40%, -45%);
      width: 54.8%;
    }
    @media (min-width: 1500px) {
      transform: translate(50%, -45%);
    }
  }
  .section__wrapper {
    position: relative;
  }
}

.homepage-section--6 {
  .section__wrapper--two-column__1 {
    padding: 24px 15px;
    border-radius: 10px;
    background: rgba(#5181B8, 0.1);
    text-align: center;
    width: 100%;
    max-width: 450px;
    @media (min-width: 450px) {
      padding: 24px 30px;
    }
    @media (min-width: 900px) {
      width: 450px;
      max-width: none;
      margin: 0 15px;
    }
    @media (min-width: 1041px) {
      margin: 0 auto;
    }
  }
  form {
    margin-top: 10px;
  }
  .input--with-icon, .btn {
    width: 100%;
    margin-bottom: 10px;
  }
  .input--with-icon {
    background: #F7F7F7;
  }
  .btn {
    padding-top: 0;
    padding-bottom: 0;
    line-height: (64em/22);
  }
  .more-width {
    @media (min-width: 450px) {
      margin-right: -20px;
      margin-left: -20px;
    }
  }
  .section__wrapper--two-column__2 {
    flex-shrink: 1;
    margin-top: 30px;
    max-width: 100%;
    @media (min-width: 900px) {
      margin-top: 0;
    }
    img {
      max-width: 100%;
      height: auto;
    }
  }
  .section__wrapper {
    flex-direction: column;
    margin: 0;
    @media (min-width: 900px) {
      margin: 0 -15px;
      flex-direction: row;
    }
  }
}