.personal-account {
  height: 100%;
  &__main {
    max-width: 1335px;
    margin: 0 auto;
    padding: 0 20px;
    &__wrapper {
      display: flex;
      align-items: center;
      flex-direction: column;
      @media (min-width: 1300px) {
        align-items: flex-start;
        flex-direction: row;
        min-height: 80vh;
      }
    }
    &__nav {
      width: 287px;
      flex-shrink: 0;
      order: 1;
      margin-top: 50px;
      @media (min-width: 1300px) {
        margin-right: 69px;
        margin-top: 0;
        order: 0;
      }
      a {
        padding: 20px 34px;
        border-radius: 5px;
        font-weight: 500;
        margin-bottom: 38px;
        box-shadow: 0px 22px 44px -12px rgba(0, 0, 0, 0.35);
        color: $color-important-text;
        transition: background-color 0.2s, color 0.2s, box-shadow 0.2s;
        overflow: hidden;
        &:last-child {
          margin-bottom: 50px;
        }
        .path {
          stroke: $color-important-text;
          transition: stroke 0.2s;
        }
        .react {
          fill: $color-important-text;
          transition: fill 0.2s;
        }
        &.active, &:hover {
          color: #ffffff;
          box-shadow: 0px 22px 44px -12px rgba(27, 147, 225, 0.35);
          background: $color-important-text;
          .path {
            stroke: #ffffff;
          }
          .react {
            fill: #ffffff;
          }
        }
        svg {
          margin-right: 20px;
        }
      }
    }
    &__content {
      background: rgba(#5181B8, 0.07);
      flex: 1 1 auto;
      padding: 26px 18px;
      width: 100%;
      max-width: 1000px;
      @media (min-width: 650px) {
        padding: 26px 38px;
      }
      @media (min-width: 1300px) {
        max-width: none;
      }
      h1 {
        font-weight: 300;
      }
      & > h1 {
        margin-bottom: 33px;
      }
      &__title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: -10px -20px;
        flex-wrap: wrap;
        margin-bottom: 30px;
        & > * {
          margin: 10px 20px;
        }
      }
      .btn--exit {
        color: #FF0000;
        font-size: (14rem/16);
        display: flex;
        align-items: center;
        svg {
          margin-left: 9px;
        }
      }
    }
  }
}
.subheader {
  display: flex;
  align-items: center;
  padding-top: 33px;
  padding-bottom: 28px;
  flex-direction: column;
  @media (min-width: 600px) {
    justify-content: space-between;
    flex-direction: row;
  }
  &__logo {
    font-size: 32px;
    color: #11C4DD;
    font-weight: 900;
    margin-bottom: 20px;
    @media (min-width: 600px) {
      margin-bottom: 0px;
    }
  }
  &__right {
    &__bill {
      vertical-align: middle;
      .price {
        font-weight: 500;
      }
    }
    &__notif {
      position: relative;
      display: inline-block;
      padding: 5px 3px;
      vertical-align: middle;
      margin: 0 13px;
      .circle {
        display: block;
        width: 18px;
        height: 18px;
        background: #ff0000;
        border-radius: 50%;
        color: #ffffff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        font-size: 14px;
        position: absolute;
        top: 0;
        right: 0;
      }
    }
    &__name {
      vertical-align: middle;
    }
  }
}

.btn-personal {
  font-size: (15rem/16);
  padding: 17px 57px;
  @media (min-width: 1000px) {
    font-size: (17rem/16);
  }
}

.personal-toggle {
  border-radius: 5px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  background: #ffffff;
  position: relative;
  & + & {
    margin-top: 15px;
  }
  &__header{
    display: flex;
    align-items: flex-start;
    font-weight: 300;
    padding: 15px 0px;
    margin: 0 19px;
    border-bottom: 1px solid rgba(#000000, 0.1);
    cursor: pointer;
    flex-direction: column;
    @media (min-width: 650px) {
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }
    &__name {
      display: flex;
      align-items: center;
      max-width: 100%;
      span {
        margin-right: 25px;
        max-width: 100%;
      }
      &__arrow{
        margin-right: 10px;
        $width-line: 8px;
        $height-line: 2px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: $width-line * 2;
        box-sizing: initial;
        &:before, &:after {
          content: '';
          display: inline-block;
          width: $width-line;
          height: $height-line;
          background: #000;
          transition: transform 0.2s;
        }
        $trasform-px: 1px;
        &:before {
          border-radius: 8px 0 0 8px;
          transform: translate($trasform-px, 0) rotate(35deg);
        }
        &:after {
          border-radius: 0 8px 8px 0;
          transform: translate(-$trasform-px, 0) rotate(-35deg);
        }
      }
    }
    &__status {
      margin-top: 5px;
      align-self: flex-end;
      @media (min-width: 650px) {
        align-self: center;
        margin-top: 0;
      }
      &.yellow {
        color: #FFE600;
      }
      &.green {
        color: #00ff1a;
      }
      &.red {
        color: #ff0000;
      }
    }
    &.active .personal-toggle__header__name__arrow{
      $trasform-px: 1px;
      &:before {
        transform: translate($trasform-px, 0) rotate(-35deg);
      }
      &:after {
        transform: translate(-$trasform-px, 0) rotate(35deg);
      }
    }
  }
  &__content {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    transition: max-height 0.4s;
    display: flex;
    flex-direction: column;
    height: 600px;
    &.init {
      position: static;
      opacity: 1;
      visibility: visible;
      max-height: 0;
    }
    &.active {
      max-height: 450px;
      @media (min-width: 600px) {
        max-height: 300px;
      }
    }
    &__messege {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 10px 20px;
      font-size: (12rem/16);
      flex: 1 1 0%;
      overflow: auto;
      .messege {
        padding: 16px 20px 18px 20px;
        background: #97b3d4;
        border-radius: 15px;
        color: #ffffff;
        width: 402px;
        max-width: 90%;
        position: relative;
        flex: 0 0 auto;
        .tringle {
          position: absolute;
          bottom: 5px;
        }
        span {
          position: relative;
          z-index: 10;
          max-width: 100%;
        }
        .time {
          position: absolute;
          bottom: 2px;
          line-height: 1;
          font-size: (9rem/16);
        }
      }
      .messege + .messege {
        margin-top: 30px;
      }
      .u-messege {
        margin-right: auto;
        margin-left: 28px;
        .time {
          right: 15px;
        }
        .tringle {
          border-right: 25px solid #97b3d4;
          border-bottom: 10px solid #97b3d4;
          border-left: 25px solid transparent;
          border-top: 10px solid transparent;
          left: -28px;
        }
      }
      .my-messege {
        margin-right: 28px;
        margin-left: auto;
        .time {
          left: 15px;
        }
        .tringle {
          border-left: 25px solid #97b3d4;
          border-bottom: 10px solid #97b3d4;
          border-right: 25px solid transparent;
          border-top: 10px solid transparent;
          right: -28px;
        }
      }
    }
    &__footer {
      background: #F7F7F7;
      border-top: 1px solid rgba(#000, 0.1);
      padding: 8px;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      justify-content: center;
      flex: 0 0 auto;
      @media (min-width: 650px) {
        flex-wrap: nowrap;
      }
      &__file {
        position: relative;
        display: inline-block;
        overflow: hidden;
        flex: 0 0 auto;
        margin-right: 11px;
        img {
          vertical-align: top;
        }
        input {
          position: absolute;
          opacity: 0;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          cursor: pointer;
        }
      }
      &__text {
        background: #ffffff;
        border: 1px solid rgba(#000000, 0.5);
        border-radius: 8px;
        display: flex;
        padding: 10px;
        flex: 1 1 100%;
        order: 1;
        margin-top: 15px;
        flex-wrap: wrap;
        justify-content: space-around;
        @media (min-width: 450px) {
          flex-wrap: nowrap;
        }
        @media (min-width: 650px) {
          margin-top: 0;
          flex: 1 1 auto;
          order: 0;
        }
        input {
          flex: 1 1 100%;
          border: 0;
          padding: 0;
          font-size: (1rem);
          width: 100%;
          min-width: 0;
          order: -1;
          margin-top: 5px;
          margin-bottom: 15px;
          @include input-placeholder {
            opacity: 0.2;
          }
          @media (min-width: 450px) {
            flex: 1 1 auto;
            order: 0;
            margin-top: 0;
            margin-bottom: 0;
            margin-left: 18px;
          } 
        }
        button {
          margin-left: 14px;
        }
      }
      &__sound {
        flex: 0 0 auto;
        margin-left: 18px;
      }
    }
  }
}
.personal-info {
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  padding: 16px 19px;
  &__row {
    font-weight: 300;
    strong {
      font-weight: 500;
    }
    &__edit {
      display: inline-flex;
      align-items: center;
      color: #5181B8;
      font-weight: 300;
      svg {
        margin-right: 9px;
      }
    }
  }
  &__row + &__row {
    margin-top: 10px;
  }
}
.personal-history {
  &__row {
    background: #FFFFFF;
    padding: 16px 19px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    margin-bottom: 15px;
    position: relative;
    &.active {
      &:before {
        content: '1';
        position: absolute;
        width: 22px;
        height: 22px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #FF0000;
        top: 0;
        right: 0;
        border-radius: 50%;
        z-index: 1;
        color: #ffffff;
        font-size: 14px;
        transform: translate(50%, -50%);
      }
    }
    h2 {
      margin: 0;
      font-weight: 300;
      line-height: 1;
      margin-bottom: 3px;
    }
    &__date {
      position: absolute;
      bottom: 0;
      right: 0;
      font-size: 10px;
      font-weight: bold;
      opacity: 0.3;
      margin-right: 1px;
      margin-bottom: 1px;
    }
  }
}
.personal-payments {
  &__row {
    font-weight: 300; 
    input, strong {
      font-weight: 400;
    }
    strong {
      margin-left: 10px;
    }
    .card-text {
      margin-right: 10px;
    }
    &__input[type=text] {
      border: 1px solid #5181B8;
      padding: 3px 9px;
      text-align: center;
      width: calc(3em);
      margin-left: 10px;
    }
    &__card[type=text] {
      border: 1px solid #5181B8;
      padding: 3px 1px;
      text-align: center;
      width: calc(3em);
      margin-left: -1px;
    } 
    &__remember {
      display: flex;
      align-items: center;
      margin: 15px 20px 15px 0;
      .box {
        width: 25px;
        height: 25px;
        border: 1px solid #000000;
        margin-left: 10px;
        cursor: pointer;
        &:checked {
          background: url("../img/goal.png") no-repeat center center;
        }
      }
    }
    &__card:first-child {
      margin-left: 0;
    }
    .card {
      display: inline-flex;
      align-items: center;
    }
  }
  &__row + &__row {
    margin-top: 10px;
  }
  &__row--button {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    .btn {
      font-size: (17rem/16);
      background: #5181B8;
      color: #ffffff;
      border: 0;
      min-width: 0;
    } 
  }
}
.personal-payments, .personal-payments__item {
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 15px 19px;
}
.personal-payments__toggle {
  margin-bottom: 15px;
  .personal-toggle__header__name span {
    margin-right: 0;
  }
  .personal-toggle__header {
    border-bottom: 0;
    margin: 0;
    padding-right: 19px;
    padding-left: 19px;
    position: relative;
    z-index: 2;
    border-radius: 5px;
    transition: box-shadow 0.4s;
  }
  .personal-toggle__header.active {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
  .personal-toggle__content {
    background: #F7F7F7;
    span {
      padding: 17px 19px;
      font-weight: 300;
    }
  }
}
.personal-payments__item, .personal-payments__toggle  {
  position: relative;
  .date {
    position: absolute;
      bottom: 0;
      right: 0;
      font-size: 10px;
      font-weight: bold;
      opacity: 0.3;
      margin-right: 3px;
      margin-bottom: 1px;
  }
}
.personal-payments__item .h2 {
  font-weight: 300;
}