.header {
  background: $color-background;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $color-white-text;
  font-size: (14rem/16);
  flex-wrap: wrap;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  @media (min-width: 620px) {
    flex-wrap: nowrap;
  }
  &__logo, &__phone {
    margin: 0 auto;
    @media (min-width: 620px) {
      margin: 0;
    }
  }
  &__center {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex: 1 1 auto;
    order: 1;
    padding-top: 15px;
    margin: -4px 0;
    @media (min-width: 620px) {
      padding: 0px 15px;
      order: 0;
    }
    &__li {
      text-align: center;
      &__a {
        margin: 4px 1em;
        position: relative;
        display: inline-block;
        @media (min-width: 700px) {
          margin: 4px calc(1em + 7vw - 49px);
        }
        @media (min-width: 885px) {
          margin: 4px calc(1em + 13px);
        } 
        &:before {
          content: '';
          display: block;
          position: absolute;
          bottom: -2px;
          left: 0;
          width: 100%;
          background: none;
          height: 1px;
          background: $color-white-text;
          transition: transform 0.2s, font-weight 0.2s;
          transform: scaleX(0);
          transform-origin: 100% 0%;
        }
        &.active {
          &:before {
            transform-origin: 0% 0%;
            transform: scaleX(1);
          }
        }
        @media (min-width: 1000px) {
          &:hover {
            &:before {
              transform-origin: 0% 0%;
              transform: scaleX(1);
            }
          }
        }
        &.active {
          font-weight: bold;
        }
      }
    }
  }
  &__phone {
    white-space: nowrap;
  }
}